import {check} from "./Auth0Update";
import config from "./paypal_config.json";

export const sendPaypalRequest = (email,name,user,logout) => {
    console.log(email);
    console.log(name);
    // Connect to API
    var paypal = require('paypal-rest-sdk');
    let clientId = config.clientId;
    let clientSecret = config.clientSecret;
    var dotenv = require('dotenv');
    dotenv.config();
    console.log(process.env);
    paypal.configure({
      'mode': 'sandbox', //sandbox or live
      'client_id': String(clientId),
      'client_secret': String(clientSecret)
    });

    // Construct a request object and set desired parameters
    // Here, OrdersCreateRequest() creates a POST request to /v2/checkout/orders
    var sender_batch_id = Math.random().toString(36).substring(9);

    var create_payout_json = {
        "sender_batch_header": {
            "sender_batch_id": sender_batch_id,
            "email_subject": "World Feeding World"
        },
        "items": [
            {
                "recipient_type": "EMAIL",
                "amount": {
                    "value": 1.00,
                    "currency": "USD"
                },
                "receiver": email,
                "note": name+", I am very happy to know that during your tough times, WFW was able to help put food on the table. This is only possible due to the donations made, and hopefully, when you are back on your feet, you will be able to give back to the WFW community as well! Enjoy your Paypal funds that can be instantly turned into gift cards for select grocerry stores and restaraunts.",
                "sender_item_id": "item_1"
            }
        ]
    };

    paypal.payout.create(create_payout_json, function (error, payout) {
        if (error) {
            console.log("ERROR");
            console.log(error.response);
            throw error;
        } else {
            console.log("Create Payout Response");
            console.log(payout);
            check(user, logout);

        }
    });
    
  }