import React from "react";
import { Container, Row, Col, Media} from "reactstrap";
import { Form, Button } from "react-bootstrap";
import { Formik } from 'formik';
import * as yup from 'yup';
import Loading from "../components/Loading";
import { useAuth0 } from "../react-auth0-spa";
import { sendPaypalRequest } from "../paypalSDK";

const Profile = () => {
  const { loading, user, logout } = useAuth0();

  if (loading || !user) {
    return <Loading />;
  }

  const schema = yup.object({
    name: yup.string().required(),
    email: yup.string()
    .email("*Must be a valid email adress")
    .required("*Email is required"),
    cemail: yup.string()
    .oneOf([yup.ref('email'),null],"*Emails do not match"),
    giftcard: yup.string()
    .required()
    .min(4,"*Select")
    // thankyou: yup.string()
    // .required()
  });

  return (
    <div>
      <button onClick={sendPaypalRequest}>Paypal</button>
      <Container className="mb-5">
        <Row className="align-items-center text-center">
          <Col>
            <Media src="https://pics.paypal.com/00/c/gifts/us/wmt2.png" className="w-75 h-75"/>
          </Col>
          <Col>
            <Media src="https://www.paypalobjects.com/digitalassets/c/gifts/media/catalog/product/s/a/safeway_xxlweb.png" className="w-75 h-75"/>
          </Col>
          <Col>
            <Media src="https://www.paypalobjects.com/digitalassets/c/gifts/media/catalog/product/b/u/burger_king_1.png" className="w-75 h-75"/>
          </Col>
        </Row>
      </Container>
      <Formik
        initialValues={{name:"",email:"",cemail:"",giftcard:""}}//,thankyou:""}}
        validationSchema={schema}
        onSubmit={(values, {setSubmitting, resetForm}) => {
          // addThankyou(values.thankyou);
          setSubmitting(true);
          setTimeout(() => {
            //alert(JSON.stringify(values, null, 2));
            sendPaypalRequest(values.email,values.name, user, logout);
            resetForm();
            setSubmitting(false);
          }, 500);
          
          //check();
        }}
      >
        {}
        {({values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting}) => (
      <Form onSubmit={handleSubmit}>
            <Form.Group as={Row} controlId="formEmail">
                <Form.Label column sm={2}>
                Email
                </Form.Label>
                <Col sm={10}>
                <Form.Control 
                  type="email" 
                  name="email"
                  placeholder="Email"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.email} 
                  className={touched.email && errors.email ? "block-example border border-danger" : null} />
                </Col>
            </Form.Group>
            <Form.Group as={Row} controlId="formcEmail">
                <Form.Label column sm={2}>
                Confirm Email
                </Form.Label>
                <Col sm={10}>
                <Form.Control 
                  type="email" 
                  name="cemail"
                  placeholder="Confirm Email"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.cemail}
                  className={touched.cemail && errors.cemail ? "block-example border border-danger" : null} />
                </Col>
            </Form.Group>
            <Form.Group as={Row} controlId="formName">
                <Form.Label column sm={2}>
                Name
                </Form.Label>
                <Col sm={10}>
                <Form.Control 
                  type="text"
                  name="name"
                  placeholder="Name"
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.name}
                  className={touched.name && errors.name ? "block-example border border-danger" : null}
                   />
                </Col>
            </Form.Group>
            <Form.Group as={Row} controlId="formGiftcard">
              <Form.Label column sm={2}>Select Giftcard</Form.Label>
              <Col sm ={10}>
              <Form.Control as="select"
                name="giftcard"
                onChange={handleChange}
                value={values.giftcard}
                className={touched.giftcard && errors.giftcard ? "block-example border border-danger" : null}>
                <option value="">Choose...</option>
                <option value="walmart">Walmart - $10</option>
                <option value="safeway">Safeway - $10</option>
                <option value="burgerking">Burger King - $10</option>
              </Form.Control>
              </Col>
            </Form.Group>
            {/* <Form.Group as={Row} controlId="formThankyou">
                <Form.Label column sm={2}>
                Thank you
                </Form.Label>
                <Col sm={10} >
                <Form.Control 
                  type="text"
                  name="thankyou"
                  placeholder="Thank you letter..."
                  onChange={handleChange}
                  onBlur={handleBlur}
                  value={values.thankyou}
                  className={touched.thankyou && errors.thankyou ? "block-example border border-danger" : null}
                   />
                </Col>
            </Form.Group> */}
            <Form.Group as={Row}>
                <Col sm={{ span: 10, offset: 11}}>
                <Button type="submit" disabled={isSubmitting}>Submit</Button>
                </Col>
            </Form.Group>
        </Form> 
        )}
      </Formik>
    </div>
    
  );
};

export default Profile;
