import config from './auth0_config.json';
export const check = (user, logout) => {


    const logoutWithRedirect = () => {
        logout({
          returnTo: window.location.origin
        });
      }
      
    var request = require("request");
    
    const aws = require('aws-sdk');

    let s3 = new aws.S3({
      accessKeyId: process.env.auth0clientid,
      secretAccessKey: process.env.auth0clientsecret
    });
    var options = { method: 'POST',
      url: 'https://dev-vglls3oa.auth0.com/oauth/token',
      headers: { 'content-type': 'application/json' },
      body: '{"client_id":'+s3.accessKeyId+',"client_secret":'+s3.secretAccessKey+',"audience":"https://dev-vglls3oa.auth0.com/api/v2/","grant_type":"client_credentials"}' };
  
    request(options, function (error, response, body) {
      if (error) throw new Error(error);
      var myJSON = JSON.parse(body);
      var request = require("request");
  
      var options = {
        method: 'PATCH',
        url: 'https://dev-vglls3oa.auth0.com/api/v2/users/'+String(user.sub),
        headers: {authorization: 'Bearer '+myJSON.access_token, 'content-type': 'application/json'},
        body: {app_metadata: {lastUse: new Date().getTime()}},
        json: true
      };
  
      request(options, function (error, response, body) {
        if (error) throw new Error(error);
        else{
          logoutWithRedirect();
        }
  
      });
  
    });
  }